@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Roboto:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,400;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav {
  width: 100%;
  padding: 1em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
}

.logo {
  color: #ec6998;
  font-size: 2em;
  font-family: "Dancing Script", cursive;
}

.nav-links {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.connect-btn {
  background-color: #ec6998;
  color: #ffffff;
}

.zap {
  width: 100%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.switch {
  border-radius: 2em;
  background-color: #282c34;
  width: 100%;
  margin: 0 0 0.5em 0;
}

.switch button {
  width: 50%;
  background-color: #282c34;
  color: #ec6998;
  text-transform: capitalize;
}

.switch button.active-switch {
  background-color: #fff;
}

.list-info {
  display: flex;
  justify-content: space-between;
  padding: 0.5em 0;
  font-weight: lighter;
  color: #fff;
}

.input-group {
  width: 100%;
  background-color: #282c34;
  border-radius: 2em;
  height: 3em;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  margin: 1em 0;
}

.input-group input {
  background-color: transparent;
  border: 0;
  width: 82%;
  height: 100%;
  padding: 0 1em;
  box-sizing: border-box;
  outline: 0;
  font-size: 1.2em;
}

.input-group button {
  width: 30%;
}

.zap .connect-btn {
  margin: 2em 0;
  text-transform: uppercase;
  width: 40%;
  align-self: center;
}

.admin {
  width: 100%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.admin h1,
h5 {
  margin: 0.5em 0;
}

.card .analytic {
  padding: 1em 0;
}

.admin h5,
small {
  color: #ec6998;
}

.zap .connect-btn {
  margin: 2em 0;
  text-transform: uppercase;
  width: 40%;
  align-self: center;
}

.App {
  text-align: center;
  background-color: #282c34;
  width: 100%;
  height: 100vh;
  color: #cfcfcf;
  padding: 0 10em;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
}

button {
  border: 0;
  padding: 1em 2em;
  border-radius: 2em;
  font-weight: 700;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: #cfcfcf;
  padding: 0 1em;
  font-weight: 700;
}

.card {
  width: 500px;
  background-color: #28252b;
  padding: 3em;
  border-radius: 2em;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

